$(window).on('load resize',windowSize);
function windowSize() {
    if ($('div').is('.product-slide__wrapper')) {
        var slideHeight = $('.product-slide__item').innerHeight();
        $('.product-slide__wrapper').css('height', '' + slideHeight + 'px');
    }
}

$('.main .offers__list').slick({
    infinite: false,
    slidesToShow: 2,
    slidesToScroll: 2,
    variableWidth: true,
    arrows: false,
    dots: false
});
$('.fast-catalog.desc-hidden').slick({
    infinite: false,
    slidesToShow: 1,
    slidesToScroll: 1,
    variableWidth: true,
    arrows: false,
    dots: false
});
if ($('section').is('.link-catalog')) {
    $('.fast-catalog').slick({
        infinite: false,
        slidesToShow: 1,
        slidesToScroll: 1,
        variableWidth: true,
        arrows: false,
        dots: false
    });
}
